/* font family */

/* styled html tags */
/* use rem/em for mobile scaling  1em is 16px.*/
@font-face {
  font-family: bodyText;
  font-display: swap;
  src: local('bodyText'), url('./assets/fonts/IndieFlower-Regular.ttf') format('truetype');
  }

  @font-face {
    font-family: headers;
    font-display: swap;
    src: local('headers'), url('./assets/fonts/PermanentMarker-Regular.ttf') format('truetype');
  }
  @font-face {
    font-family: ptags;
    font-display: swap;
    src: local('ptags'), url('./assets/fonts/FunnelSans.ttf') format('truetype');
  }


h1, h2 {
  font-family: headers;
  letter-spacing: 3.5px;
    color: var(--color-terracota); 
    margin: auto;

}

h3, h4, h5{
  font-family: bodyText;
  margin: 0px;
}

h1{
font-weight: 900;
font-size: 2.5em;
}

h2{
  font-weight: 600;
  font-size: 2em;
  color: var(--color-turmeric);
}

h3, h4, h5{
  font-size: 1.5em;
  color: var(--color-sage)
}

p{
font-size: 1rem;
font-family: ptags;
color: var(--color-charcoal);
margin: 0px;
}

a{
  color: var(--color-charcoal);
  text-decoration: none;
  font-family: ptags;
  font-size: 1rem;
}

button{
  text-decoration: none;
  font-family: ptags;
}
input:focus{
  border-color: var(--color-sage);
}
