.search-input-container{
    max-width: 1200px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 20px 0px;
    margin: auto;
}

.search-input{
    width: 100%;
    width: 525px;
    padding: 15px;
    border: 1px solid gray;
    border-radius: 16px;
}
.filter-input-container{
    max-width: 1200px;
    width: 100%;
    display: flex;
    justify-content: start;
    padding: 20px 0px;
    margin: auto;
    align-items: center;
}
button{
    background-color: var(--color-terracotta);
    border-radius: 12px;
    color: var(--color-linen);
    border: none;
    max-width: 150px;
    width: 100%;
    padding: 10px;
    text-transform: uppercase;
}
.tag-filter{
    display: flex;
    flex-direction: row;
    align-items:center;
    justify-content: space-between;
}
.tag-filter button{
    text-align: center;
    max-width: 125px;
    padding: 0px 10px;
    height: 40px;
    border-radius: 26px;
    width: 100%;
    min-width: 100px;
    margin: 0px 5px;
}

.results-grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 equal-width columns */
    grid-template-rows: repeat(3, auto);   /* 3 rows that size to content */
    gap: 20px; /* space between grid items */
    padding: 20px;
    max-width: 1200px;
    width: 100%;
    margin: auto; 
}

@media screen and (max-width: 1080px) {
    .results-grid{
       grid-template-columns: repeat(2, 1fr); /* 3 equal-width columns */
    grid-template-rows: repeat(3, auto);   /* 3 rows that size to content */
    }
}