.head-container{
    display: flex;
    position: absolute;
    top: 0px;
    height: 80px;
    z-index: 1000;
    width: 100%;
    background-color: rgba(248, 245, 242, 0.8); /* Linen tint overlay */
    position: sticky;
    top: 0px;
}

.head-container:hover{
      background-color: rgba(248, 245, 242); /* Linen */

}

.head-content{
    max-width: 1440px;
    width: 100%;
    justify-content: flex-end;
    display: flex;
    align-items: center;
}

nav {
    display: flex;
    justify-content: space-evenly;
    width: 450px;
}

nav a {
    height: 30px;
    width: 100px;
    background-color: var(--color-turmeric);
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    border-radius: 6px;
    box-shadow: 1px 5px 0px var(--color-terracotta);
}

img#logo{
    filter: brightness(0) saturate(100%) invert(60%) sepia(13%) saturate(1553%) hue-rotate(328deg) brightness(89%) contrast(87%);

}

@media print {
  .head-container{
  display: none;
  }
}