.hero-bg {
  background-size: cover;
    background-position: bottom;
    width: 100%;
    min-height: 500px;
    max-height: 500px;
    height: 100%;
    margin-top: -80px;
    display: grid;
    place-items: center;
    background-color: var(--color-overlay);
    background-blend-mode: overlay;
}

.grid-1-col{
    grid-column: 1;
    text-align: center;
}
.grid-1-col p{
  color: var(--color-white);
}
@media print {
  .hero-bg{
  display: none;
  }
}