.mainbody{
    min-height: 1500px;

}

.section_img {
    max-width: 450px;
    width: 100%;
    border-radius: 12px;
    position: absolute;
    top: -100px;
    z-index: 10;
    object-fit: contain;
    right: -150px;
}

.firstsectioncard{
    border-radius: 12px;
    column-count: 1;
    max-width: 400px;
    width: 100%;
    background-color: #D9DDD4;
    text-align: center;
    padding: 20px;
    position: relative;
    left: 40px;
    top: -130px;
    z-index: 1;
    place-content: center;
}
.firstsectioncard p{
    margin: 10px 0px;
}
.secondsectioncard{
    column-count: 1;
    max-width: 500px;
    height: 225px;
    width: 100%;
    background-color: #9CA89A;
    text-align: center;
    padding: 20px;
    position: relative;
    right: 250px;
    z-index: 0;
    top: 35px;
    border-radius: 12px;
}
.midGrid{
    display: grid;
    grid-template-columns: repeat(1, 1fr); /* 3 equal-width columns */
    max-width: 1200px;
    width: 100%;
    margin: auto;
    padding: 0px 0px;
    height: 275px;
    justify-items: center;
}
.midCol{
    background-image: url('https://cdn.sanity.io/images/jvie9x8w/production/1a9a350c6265db6362174e5cb9fac897e7572a7c-1254x836.webp');
    background-size: cover;
    position: relative;
    background-repeat: no-repeat;
    height: 375px;
    max-width: 600px;
    width: 100%;
    place-content: center;
    border-radius: 275px 85px;
}
.midButton{
    width: 250px;
    border-radius: 6px;
    font-family: ptags;
    height: 50px;
    background-color: var(--color-terracotta);
    color: var(--color-linen);
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    left: -60px;
}
h1{
    color: var(--color-linen);
}
.goToSearch{
    background-color: var(--color-mustard);
    height: 30px;
    width: 50%;
    margin: auto;
    padding: 10px 0px;
    display: block;
    border-radius: 6px;
    color: var(--color-linen);
    place-content: center;
}
.reversefirstsectioncard{
    border-radius: 12px;
    column-count: 1;
    order: 2;
    max-width: 400px;
    height: 225px;
    width: 100%;
    background-color: var(--color-light-turmeric);
    text-align: center;
    padding: 20px;
    position: relative;
    left: 40px;
    top: 30px;
    z-index: 1;
    place-content: center;
}
.reversesecondsectioncard{
    column-count: 1;
    max-width: 450px;
    order: 1;
    height: 225px;
    width: 100%;
    background-color: var(--color-mustard);
    text-align: center;
    padding: 20px;
    position: relative;
    left: 50px;
    border-radius: 12px;
    z-index: 1;
    top: 100px;
}
.reversesection_img {
    max-width: 500px;
    width: 100%;
    border-radius: 12px;
    position: absolute;
    top: 100px;
    z-index: 1000;
    object-fit: contain;
    left: 200px;
}

@media screen and (max-width: 1080px){
    
    .section_img {
        position: unset;
    }

    .firstsectioncard{
        position: unset;
        height: auto;  
        padding: 65px;   
    }
    .secondsectioncard{
        position: unset;
        height: auto;
    }
    .midGrid{
        max-width: 425px;
        height: auto;
    }
    .midCol{
        position: unset;
    }
    .midButton{
 
    }
    .goToSearch{
    }
    .reversefirstsectioncard{     
        position: unset;
        height: auto;
        padding: 70px;
    }
    .reversesecondsectioncard{
        height: auto;
        max-width: 500px;
        position: unset;
    }
    .reversesection_img {
        position: unset;
    }
}