.card-container{
    column-count: 1;
    width: 100%;
    max-width: 375px;
    height: 450px;
    max-height: 450px;
    display: grid;
    border: 1px solid gray;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(47, 47, 47, 0.15); /* subtle charcoal shadow */
    grid-template-columns: repeat(1, 2fr);
}
.card-container img{
    width: inherit;
    object-fit: cover;
    height: 200px;
    border-radius: 12px 12px 0px 0px;
}
.image-container{
    width: inherit;
    height: 200px;
    position: relative;
}
.badge-container{
    position: absolute;
    width: 100%;
    max-width: 100px;
    bottom: 70px;
    max-height: 200px;
    display: grid;
    align-items: center;
    justify-content: space-around;
}
.recipe-badge{
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 5px 0px;
    width: 100px;
    text-align: center;
    margin: 2.5px 0px;
    border-radius: 0px 4px 4px 0px;
}
#vegan.recipe-badge {
background-color: var(--color-sage);
}
#lowC.recipe-badge{
    background-color: var(--color-terracotta);
}
#gf.recipe-badge{
    background-color: var(--color-turmeric)
}
#veg.recipe-badge{
    background-color: var(--color-charcoal)
}
.card-content{
    width: 90%;
    padding: 0px 10px;
    margin: 0px auto;
    height: 250px;
    display: grid;
    align-content: space-around;
}
.card-description{
  display: -webkit-box;
  -webkit-line-clamp: 2;      /* Limit to 2 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.4em;        /* set a consistent line height */
  max-height: calc(1.4em * 2); /* 2 lines Ã— line-height */
}

.ingredient-ul{
    padding: 5px;
    display: flex;
    justify-content: space-evenly;
    text-transform: capitalize;
}
.ingredient-ul li{
    list-style: none;
}
h3#cardName{
    text-transform: capitalize;
}
#viewmore{
    background-color: var(--color-terracotta);
    border: none;
    border-radius: 12px;
    padding: 10px 8px;
}