.section-grid{
    display: grid;
    align-content: center;
    max-width: 800px;
    width: 100%;
    margin: auto;
}
.sectionCol1{
    margin: auto;
    column-count: 1;
}
.sectionCol1 p{
    margin: 20px 0px;
}
.timeRow{
    display: flex;
    justify-content: space-between;
    max-width: 775px;
    width: 100%;
    margin: 20px auto;
    font-weight: 700;
}
.ingredientCol{
    column-count: 1;
    max-width: 940px;
}
.detail-image{
    margin: auto;
    z-index: 5;
    margin-top: -200px;
}
img.detail-image{
 height: 400px;
 width: 800px;
 border-radius: 80px 0px 80px 0px;
}
.sectionCol2{
    margin: auto;
    column-count: 2;
}
.detail-container{
    max-width: 1200px;
    margin: auto;
}

ol {
    list-style: none;
}

ol li {
    padding: 10px 0px ;
}
